.postcard {
  background-image: url('../../images/card-paper.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 80%;
  background-clip: border-box;
  width: 100%;
  min-height: 400px;
  position: relative;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
}

@media screen and (max-width: 1140px) {
  .postcard {
    min-height: 300px;
    background-size: contain;
  }
}
